<template>
  <div>
    <content-title
      title="招聘消息"
      tips="此处显示系统消息，绿色代表普通消息，橙色代表提醒消息，红色代表错误消息。">
    </content-title>
    <content-c>
      <el-table
        :data="msgList"
        style="width: 100%">
        <el-table-column
          prop="grade"
          label="消息等级">
          <template slot-scope="scope">
            <div>
              <div v-if="scope.row.gradeId === msgGradeCpt.gradeId1"
                   style="width: 15px;height: 15px;border-radius: 50%;background-color: #5AD1D1; margin-left: 18px;"></div>
              <div v-if="scope.row.gradeId === msgGradeCpt.gradeId2"
                   style="width: 15px;height: 15px;border-radius: 50%;background-color: #EB6941; margin-left: 18px;"></div>
              <div v-if="scope.row.gradeId === msgGradeCpt.gradeId3"
                   style="width: 15px;height: 15px;border-radius: 50%;background-color: #DB0000; margin-left: 18px;"></div>

            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="标题">
        </el-table-column>
        <el-table-column
          prop="time"
          label="时间">
          <template slot-scope="scope">
            {{getTimeFormat(scope.row.sendTime)}}
          </template>
        </el-table-column>
        <el-table-column
          width="500"
          label="消息内容"
        >
          <template slot-scope="scope">
            <p class="com-a-line">
              {{scope.row.msg}}
            </p>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          width="160">
          <template slot-scope="scope">
            <button class="com-btn com-detail" :class="{read: scope.row.read}"
                    @click="handleDetail(scope.row, scope.column, scope.$index)">查看
            </button>
          </template>
        </el-table-column>
      </el-table>

      <div class="com-page-box">
        <div class="com-pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="msgListTotal"
            v-if="msgListTotal > pageSize">
          </el-pagination>
        </div>
      </div>
    </content-c>
    <!--消息内容-->
    <com-dialog
      :show="msgDetailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p>
          <span class="label">标&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;题: </span>
          {{currentMstDetail.title}}
        </p>

        <p><span class="label">时&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;间: </span>
          {{getTimeFormat(currentMstDetail.sendTime)}}
        </p>

        <p class="com-dashed-line"></p>

        <p>
          <span class="label label-block">内&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;容:</span>
          <p class="msg">{{currentMstDetail.msg}}</p>
        </p>
      </div>

      <div slot="title" class="com-dialog-title t-1">
        消息内容
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #45C5C0; border-color: #45C5C0;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import comDialog from '../../../components/toast/dialog.vue'
  import { mapState, mapActions } from 'vuex'
  import { msgType, msgGrade } from '../../../utils/baseConfig'
  import { formatDateTime } from '../../../utils/utils'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        msgDetailDialogVisible: false,
        currentMstDetail: {},
      }
    },
    computed: {
      ...mapState('message', [
        'msgList',
        'msgListTotal',
      ]),
      msgGradeCpt () {
        return msgGrade
      }
    },
    methods: {
      ...mapActions('message', [
        'ac_msgList',
        'ac_msgView'
      ]),
      getTimeFormat (timeStamp) {
        return formatDateTime(timeStamp)
      },
      handleDetail (row) {
        this.currentMstDetail = row
        this.msgDetailDialogVisible = true
        this.ac_msgView({msgId: row.id})
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_msgList({
          entity: {typeId: msgType.entryId},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_msgList({
          entity: {typeId: msgType.entryId},
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      closeDialog () {
        this.msgDetailDialogVisible = false
        this.ac_msgList({
          entity: {typeId: msgType.entryId},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      }
    },
    components: {
      contentTitle,
      contentC,
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.ac_msgList({
        entity: {typeId: msgType.entryId},
        pageNo: this.currentPage - 1,
        pageSize: this.pageSize
      })
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";

  .t-1 {
    background-color: #45C5C0;
  }

  .x-color-1 {
    color: #45C5C0;
  }

  .read {
    opacity: 0.4;
    /*background: rgba(255,255,255, 0.2);*/
  }

  .resume-detail {
    p {
      line-height: 40px;
    }
    .label {
      margin-right: 20px;
      &.label-block {
        display: block;
      }
    }
    .msg {
      display: block;
      text-indent:25px
    }
  }
</style>



